import React, { Component } from 'react';
import { Redirect } from 'react-router';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import EntityActions from '../Redux/EntityRedux';
import OutcomeForm from './OutcomeForm';
import TransactionActions from '../Redux/TransactionRedux';
import DataViewActions from '../Redux/DataViewRedux';

class OutcomeContainer extends Component {
    state = {
        entity:{
            id: localStorage.getItem('entity_selected_id'),
            name: localStorage.getItem('entity_selected_name'),
        },
    }

    componentDidMount () {
        this.props.actions.view.transactionFormInit(this.state.entity.id);
    }

    isLoadingData = () => {
        return this.props.view.data === null || !this.props.view.data.accounts || !this.props.view.data.business_centers;
    }

    render() {
        const options = {
        };

        const viewData = this.props.view.data;

        if (this.isLoadingData()){
            return  <h1></h1>
        }

        return (
            <OutcomeForm
                entity={this.state.entity}
                accounts={viewData.accounts}
                business_centers={viewData.business_centers}
                options={options}
                onSave={this.props.actions.transaction.createOutcomeTransaction}
                error_fields={this.props.transaction.error_fields}
            />
        )
    }
}

const mapStateToProps = (state) => ({
    entity : state.entity,
    transaction : state.transaction,
    view: state.data_view,
});

const mapDispatchToProps = (dispatch) => ({
	actions: {
        transaction: bindActionCreators(TransactionActions, dispatch),
        entity: bindActionCreators(EntityActions, dispatch),
        view: bindActionCreators(DataViewActions, dispatch),
    }
});

export default connect(mapStateToProps, mapDispatchToProps)(OutcomeContainer);


