import React, {Component} from 'react';
import {
    Card, CardBody, CardHeader, CardFooter,
    Row, Col,
    Label, Input, FormGroup, Button,
    FormFeedback
} from 'reactstrap';
import moment from 'moment';
import 'react-widgets/dist/css/react-widgets.css';
import DateTimePicker from 'react-widgets/lib/DateTimePicker';
import momentLocalizer from "react-widgets-moment";
import util, {IMAGE} from '../../src/util';

momentLocalizer(moment);

export default class OutcomeForm extends Component {
    state = {
        account_id: null,
        amount: null,
        description: null,
        date: moment().format('YYYY-MM-DD').toString(),
        business_center_id: null,
        assets: [],
    }
    changeStartDt = (date) => {
        this.setState({startDt: moment(date).format('YYYY-MM-DD').toString()});
    }
    onChangeAmount = (e) => {
        this.setState({amount: e.target.value});
    }
    onchangeBusinessCenter = (business_center_id) => {
        this.setState({business_center_id});
    }
    onchangeAccount = (account_id) => {
        this.setState({account_id});
    }
    onChangeDescription = (e) => {
        this.setState({description: e.target.value});
    }
    changeDate = (date) => {
        this.setState({date: moment(date).format('YYYY-MM-DD').toString()});
    }
    renderAlertField = (field, description) => {
        return this.invalidField(field) ?  (<FormFeedback>{description} es requerido</FormFeedback>) : null;
    }
    invalidField = (field) => {
        return this.props.error_fields.indexOf(field) >= 0;
    }
    render() {
        return (
            <Card>
                <CardHeader>
                    <strong>Nuevo egreso</strong>
                    <small> Local</small>
                </CardHeader>
                <CardBody>
                    <Col xs="12">
                        <Row>
                            <Col xs="12">
                                <FormGroup>
                                    <Label htmlFor="name">Cantidad</Label>
                                    <Input invalid={this.invalidField('amount')} onChange={this.onChangeAmount} type="text" id="name" placeholder="0" required />
                                    { this.renderAlertField('amount', 'Cantidad') }
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="12">
                                <FormGroup>
                                    <Label htmlFor="ccnumber">Centro de costo</Label>
                                    <Input
                                        invalid={this.invalidField('business_center_id')}
                                        onChange={(event) => {
                                            this.onchangeBusinessCenter(event.target.value)
                                        }}
                                        type="select"
                                        required
                                        name="select">
                                        <option key={'empty-bc-id'} value="">Seleccione un centro de costo</option>
                                        {this.props.business_centers.map(bc => {
                                            return <option key={bc.id} value={bc.id}>{bc.name}</option>
                                        })}
                                    </Input>
                                    { this.renderAlertField('business_center_id', 'Centro de Costo') }
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="12">
                                <FormGroup>
                                    <Label htmlFor="name">Descripcin</Label>
                                    <Input invalid={this.invalidField('description')} onChange={this.onChangeDescription} type="text" id="name" placeholder="" />
                                    { this.renderAlertField('description', 'Descripcin del ingreso') }
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="12">
                                <FormGroup>
                                    <Label htmlFor="name">Fecha</Label>
                                    <DateTimePicker onChange={this.changeDate} invalid={this.invalidField('date')}/>
                                    { this.renderAlertField('date', 'Fecha') }
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="12">
                                <FormGroup>
                                <Label htmlFor="ccnumber">Cuenta</Label>
                                    <Input
                                        invalid={this.invalidField('account_id')}
                                        onChange={(event) => {
                                            this.onchangeAccount(event.target.value)
                                        }}
                                        type="select" required name="select">
                                        <option key={'empty-bc-id'} value="">Seleccione una cuenta</option>
                                        {this.props.accounts.map(bc => {
                                            return <option key={bc.id} value={bc.id}>{bc.name}</option>
                                        })}
                                    </Input>
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row>
                            <Col xs="12">
                                <FormGroup>
                                    <Label htmlFor="name">Documentos</Label>
                                </FormGroup>
                            </Col>
                        </Row>
                    </Col>
                </CardBody>
                <CardFooter>
                    <div className="pull-right">
                        <button className="btn btn-default">Cancelar</button>
                        <button className="btn btn-primary" onClick={() => {
                            const {account_id, amount, description, date, business_center_id, assets} = this.state;
                            console.log(this.state);
                            this.props.onSave(account_id, amount, description, date, business_center_id, assets)
                        }}>Guardar</button>
                    </div>
                </CardFooter>
            </Card>
        );
    }
}


